module.exports = function numberFinder(string) {
  if (typeof string !== "string") throw new TypeError("Number Extractor wants a string!");

  if (!isNaN(parseInt(string.replace(/\D/g, '')))) {
    var numberTypeArray = string.match(/\d+/g).map(Number);
    var stringTypeArray = string.match(/\d+/g);
    var stringTypeNumberSet = string.replace(/\D/g, '');
    var numberTypeNumberSet = parseInt(string.replace(/\D/g, ''));
    var stringTypeNumberArray = string.replace(/\D/g, '').split('');
    var numberTypeNumberArray = string.replace(/\D/g, '').split('').map(Number);
    return {
      stringTypeNumberSet: stringTypeNumberSet,
      numberTypeNumberSet: numberTypeNumberSet,
      stringTypeArray: stringTypeArray,
      numberTypeArray: numberTypeArray,
      stringTypeNumberArray: stringTypeNumberArray,
      numberTypeNumberArray: numberTypeNumberArray
    };
  } else return {
    stringTypeNumberSet: "",
    numberTypeNumberSet: "",
    stringTypeArray: [],
    numberTypeArray: [],
    stringTypeNumberArray: [],
    numberTypeNumberArray: []
  };
};